.shipping-info {
    max-width: 800px;
    margin: 50px auto;
    padding: 20px;
    background: white;
    border-radius: 10px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    font-family: Arial, sans-serif;
  }
  
  h2 {
    text-align: center;
    font-size: 24px;
    font-weight: bold;
    color: #2874f0;
    margin-bottom: 20px;
  }
  
  .shipping-section {
    margin-bottom: 20px;
  }
  
  h3 {
    font-size: 18px;
    font-weight: bold;
    color: #333;
    margin-bottom: 5px;
  }
  
  p, ul {
    font-size: 16px;
    color: #555;
    line-height: 1.5;
  }
  
  ul {
    margin-left: 20px;
  }
  
  li {
    margin-bottom: 5px;
  }
  
  a {
    color: #2874f0;
    text-decoration: none;
  }
  
  a:hover {
    text-decoration: underline;
  }
  